import ticketTypeService from '@/services/ticketTypeService.js';
import { mobileMixin } from './../../mixins/mobileMixin.js';

export default {
	name: 'ticket-type-description',
	mixins: [mobileMixin],
	props: {
		ticketTypeId: {
			type: Number,
			default: 0
		},
		ticketTypeName: {
			type: String
		},
		price: {
			type: Number,
			default: 0
		},
		showBuy: {
			type: Boolean,
			default: false
		},
		value: {
			type: Boolean,
			default: false
		},
		shouldRead: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			description: {
				bookDescription: '',
				feeDescription: '',
				usageDescription: '',
				refundDescription: '',
				otherDescription: ''
			},
			show: this.value,
			second: 0,
			hasRead: false,
			hasLoaded: false,
			timer: -1,
			accept: false
		};
	},
	computed: {
		ticketTypePrice() {
			return this.price * 100;
		},
		allowSubmit() {
			return !this.shouldRead || this.hasRead;
		},
		submitStyle() {
			return { height: (this.shouldRead ? 90 : 50) + 'px' };
		}
	},
	watch: {
		ticketTypeId() {
			this.hasLoaded = false;
		},
		async value(val) {
			this.show = val;
			if (val) {
				if (this.showBuy && this.shouldRead) {
					this.countdown();
				}
				if (!this.hasLoaded) {
					await this.loadDescription();
				}
			} else {
				this.$refs.descriptionPopup.close();
			}
		},
		show(val) {
			if (!val) {
				if (this.showBuy) {
					this.hasRead = false;
					if (this.second > 0) {
						clearInterval(this.timer);
					}
				}
				this.$emit('input', val);
			} else {
				this.$refs.descriptionPopup.open();
			}
		}
	},
	methods: {
		onClose() {
			this.show = false;
		},
		onBuy() {
			uni.navigateTo({
				url: '/pages/ticket/buy-ticket?ticketTypeId=' + this.ticketTypeId
			});
		},
		async loadDescription() {
			const description = await ticketTypeService.getTicketTypeDescriptionAsync(this.ticketTypeId);
			this.description = description;
			this.hasLoaded = true;

			this.$nextTick(() => {
				this.normalizeImage('.description-content-item');
			});
		},
		countdown() {
			this.second = 5;
			this.timer = setInterval(() => {
				if (this.second > 0) {
					this.second--;
				} else {
					clearInterval(this.timer);
				}
			}, 1000);
		},
		popupChange(a) {
			if (!a.show) {
				this.show = false;
				this.accept = false;
			}
		},
		acceptChange(a){
			if(a.detail.value.length > 0){
				this.accept = true;
			}else{
				this.accept = false;
			}
		}
	}
};
